import React, { useState, useEffect } from "react";
import A from "./a/a";
import B from "./b/b";
import sty from  "./three.module.less";

const list = [
  { code: "1", comp: <A /> },
  { code: "2", comp: <B /> },
  { code: "3", comp: A },
  { code: "4", comp: A },
  { code: "5", comp: A },
  { code: "6", comp: A },
  { code: "7", comp: A },
  { code: "8", comp: A },
  { code: "9", comp: A },
  { code: "10", comp: A },
  { code: "11", comp: A },
  { code: "12", comp: A }
];

const Three: React.FC = () => {
  const [code, setCode] = useState<string>("1");

  console.log("ccc");

  useEffect(() => {
    console.log("ddd");
  }, []);

  const changeCode = (val: string) => {
    setCode(val);
  };


  return <div className={sty.content}>
    {
      list.map((item: any, index: number) => {
        return <div key={index} onClick={() => changeCode(item.code)}>
          {code === item.code ? item.comp : null}
        </div>;
      })
    }
  </div>;
};

export default Three;
