import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import sty from "./login.module.less";
import logo from "assets/images/logo.jpg";
import { useAction } from "store/store";
import commonFunc from "untis/common-func";

let timer: any;

const Login: React.FC = (props: any) => {
  const action = useAction();

  const [show, setShow] = useState(true);
  const [text, setText] = useState("获取验证码");
  const [mobile, setMobile] = useState("");
  const [code, setCode] = useState("");
  const [isError, setIsError] = useState(true);

  useEffect(() => {
    const info = commonFunc.getCookie("info");
    if (info) {
      setMobile(JSON.parse(info).mobile);
      setCode(JSON.parse(info).code);
    }
  }, []);

  const getMobile = (e: any) => {
    setMobile(e.target.value);
  };
  const getAuto = (e: any) => {
    setCode(e.target.value);
  };
  const getCode = () => {
    let count = 60;
    timer = setInterval(() => {
      if (count > 0 && count <= 60) {
        setShow(false);
        count--;
        const title = `${text}${count}s`;
        setText(title);
      } else {
        setShow(true);
        setText("获取验证码");
        count = 60;
        clearInterval(timer);
      }
    }, 300);
  };

  const loginAction = () => {
    clearInterval(timer);
    if (mobile === "18529533634" && code === "10086") {
      console.log(props.history);
      props.history.push("/home");
      action.changeHasAuth(true);
      sessionStorage.setItem("isLogin", "true");
      const obj = { mobile: mobile, code: code };
      commonFunc.setCookie("info", JSON.stringify(obj), 7);
      commonFunc.setCookie("token", "xulongji", 1);
    } else {
      setIsError(false);
    }
  };
  return (
    <div className={sty.content}>
      <div className={sty.main}>
        <div className={sty.nav}>
          <img alt="图标" className={sty.logo} src={logo} />
        </div>
        <div className={sty.input}>
          <div className={sty.mobile}>
            <div className={sty.left}>+86</div>
            <input
              onChange={getMobile}
              maxLength={11}
              value={mobile}
              className={sty.right}
              placeholder="请输入手机号码"
            />
          </div>
          <div className={sty.code}>
            <input
              className={sty.put}
              maxLength={6}
              value={code}
              onChange={getAuto}
              placeholder="请输入验证码"
            />
            {show ? (
              <div className={`${sty.putRig} ${sty.blueBg}`} onClick={getCode}>
                {text}
              </div>
            ) : (
              <div className={`${sty.putRig} ${sty.grayBg}`}>{text}</div>
            )}
            <div
              className={sty.show}
              style={{ display: show ? "none" : "block" }}
            >
              10086
            </div>
          </div>
        </div>
        <div className={sty.btn} onClick={loginAction}>
          登录
        </div>
        <div className={sty.error} style={{ display: isError ? "none" : "block" }}>
          账户或密码错误
        </div>
      </div>
    </div>
  );
};

export default withRouter(Login);
