import React from "react";
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";
import menuList from "./routerConfig";
import Layout from "page/commons/layout/layout";
import Login from "page/login/login";
import NotFound from "page/commons/notFound/notFound";

const AppRouter: React.FC<{ isLogin: boolean }> = ({ isLogin }) => {
  return (
    <BrowserRouter>
      <Switch>
        {!isLogin && <Route path="/login" exact component={Login} />}
        {!isLogin && (
          <Route path="*" exact render={() => <Redirect to="/login" />} />
        )}
        {isLogin && (
          <Switch>
            <Route path="/login" exact render={() => <Redirect to="/home" />} />
            <Layout>
              <Switch>
                <Route path="/notFound" exact component={NotFound} />
                {menuList.map((item, index) => (
                  <Route
                    key={index}
                    path={item.path}
                    render={() => {
                      return (
                        <Switch>
                          <Route
                            path={item.path}
                            exact
                            component={item.component}
                          />
                          <Redirect to="/" />
                        </Switch>
                      );
                    }}
                  />
                ))}
              </Switch>
            </Layout>
          </Switch>
        )}
      </Switch>
    </BrowserRouter>
  );
};

export default AppRouter;
