import React, { useState, useEffect, memo } from "react";
import { withRouter } from "react-router-dom";
import munuList from "router/routerConfig";
import ten from "assets/images/12.png";
import "./menu.less";

const Menus: React.FC = (props: any) => {
  const [selectFater, setSelectFater] = useState("");

  useEffect(() => {
    init(props.history.location.pathname);
  }, []);

  const init = (path: any) => {
    munuList.forEach((item: any) => {
      if (item.path === path) {
        setSelectFater(path);
      }
    });
  };

  const setShowAction = (path: string) => {
    setSelectFater(path);
    props.history.push(path);
  };

  const goFirstAction = () => {
    setSelectFater(munuList[0].path);
    props.history.push(munuList[0].path);
  };

  return (
    <div className="example">
      <div className="back_icon" onClick={goFirstAction}>
        <img src={ten} />
      </div>
      {munuList.map((item, index) => {
        const isSelect = item.path === selectFater;
        return (
          <div
            key={index}
            className={`item-title ${isSelect ? "selected" : ""}`}
            onClick={() => setShowAction(item.path)}
          >
            <div>
              <img src={item.icon[isSelect ? 0 : 1]} />
            </div>
            <div>{item.name}</div>
          </div>
        );
      })}
    </div>
  );
};

export default memo(withRouter(Menus));
