import React, { useEffect, useState } from "react";
import { useStore } from "store/store";
import { Select } from "antd";
import AMapLoader from "@amap/amap-jsapi-loader";
import sty from "./one.module.less";
import Tu from "assets/images/tu.png";
// const bounds = [113.667745922, 22.364291739, 114.727367497, 22.93907434]; //地图范围设置在深圳市

const One: React.FC = () => {
  const rootFontSize = useStore().rootFontSize;
  const [map, setMap] = useState<any>();
  const [pointData, setPointData] = useState<any[]>([]);
  const [markers, setMarkers] = useState<any[]>([]);
  const [filterList, setFilterList] = useState<any[]>([]);
  const [address, setAddress] = useState<any>({});
  const [obj, setObj] = useState<any>([]);

  useEffect(() => {
    getMapLoad();
  }, []);

  useEffect(() => {
    if (!map) return;
    map.remove(markers);
    const list: any[] = [];
    pointData.forEach((item: any) => {
      const marker = new AMap.Marker({
        map,
        icon: creatIcon(),
        position: [item.lng, item.lat],
        offset: new AMap.Pixel(-0.21 * rootFontSize, -0.23 * rootFontSize),
        // extData: item
      });
      marker.on("click", markerClick);
      list.push(marker);
    });
    map.add(list);
    setMarkers(list);
  }, [pointData, map]);

  const getMapLoad = () => {
    AMapLoader.load({
      key:"0d78972df8f364f4dadc885ea007be5b", //需要设置您申请的key
      version:"2.0",
      plugins:["AMap.ToolBar","AMap.Driving", "AMap.DistrictSearch", "AMap.PlaceSearch"],
      AMapUI:{
        version:"1.1",
        plugins:[],
      },
      Loca:{
        version:"2.0.0"
      },
    }).then((AMap)=>{
      const m = new AMap.Map("mapcontainer",{
        viewMode:"3D",
        zoom:13,
        zooms:[8,16],
        center:[114.208552, 22.5],
        citylimit: true,  // 是否强制限制在设置的城市内搜索
      });
      // 限制地图显示范围
      const bounds = new AMap.Bounds([113.667745922, 22.364291739], [114.727367497, 22.93907434]);
      m.setLimitBounds(bounds);
      outline(m);
      setMap(m);
    }).catch(e=>{
      console.log(e);
    });
  };

  // 创建一个 Icon
  const creatIcon = () => {
    const startIcon = new AMap.Icon({
      // 图标尺寸
      size: new AMap.Size(0.42 * rootFontSize, 0.47 * rootFontSize),
      // // 图标的取图地址
      image: Tu,
      // content: "<div style={{ width: 0.3rem, height: 0.3rem, background: red }}></div>", //设置文本标注内容
      // 图标所用图片大小
      imageSize: new AMap.Size(0.42 * rootFontSize, 0.47 * rootFontSize),
      // 图标取图偏移量
      // imageOffset: new AMap.Pixel(-9, -3)
    });
    return startIcon;
  };

  //描边
  const outline = (m: any) => {
    const options = {
      subdistrict: 0,
      extensions: "all",
      level: "city", // 搜索级别省市区等
    };
    const district = new AMap.DistrictSearch(options);
    district.search("深圳市", (status, result) => {
      if (status === "complete" && result.info === "OK") {
        const bounds = result.districtList[0].boundaries;
        const polygons: any[] = [];
        if (bounds) {
          for (let i = 0, l = bounds.length; i < l; i++) {
            //生成行政区划polygon
            const polygon = new AMap.Polygon({
              map: m,
              path: bounds[i],
              fillOpacity: 0.1,
              fillColor: "rgba(0,0,0)",
              strokeColor: "rgb(20,164,173)",
              strokeWeight: 4,
              trokeDasharray:[10,2,10]
            });
            polygons.push(polygon);
            m.add(polygon);
          }
          // 地图自适应
          m.setFitView();
        }
      }
    });
  };

  const searchPlace = () => {
    const PlaceSearchOptions = { //设置PlaceSearch属性
      city: "深圳", //城市
      type: "", //数据类别
      pageSize: 20, //每页结果数,默认10
      pageIndex: 1, //请求页码，默认1
      extensions: "base" //返回信息详略，默认为base（基本信息）
    };
    const MSearch = new AMap.PlaceSearch(PlaceSearchOptions); //构造PlaceSearch类
    MSearch.search("宝安国际机场", (status, result) => {
      console.log("abc", status, result);
      if (status === "complete" && result.info === "OK") {
        setFilterList(result.poiList.pois || []);
      } else {
        setFilterList([]);
      }
    });
  };

  const getData = () => { // 获取点位数据
    setTimeout(() => {
      const positionArr = [
        {name: "第一家企业", lng: 114.051411, lat: 22.516065, id: "1", remark: "运作良好"},
        {name: "第二家企业", lng: 114.069521, lat: 22.515669, id: "2", remark: "赚点小钱"},
        {name: "第三家企业", lng: 114.053127, lat: 22.540325, id: "3", remark: "亏本运行"},
        {name: "第四家企业", lng: 113.934638, lat: 22.533706, id: "4", remark: "已经倒闭"},
      ];
      setPointData(positionArr);
    }, 500);
  };

  const markerClick = (val: any) => {
    // const a = map.getPosition(val.lnglat.lng, val.lnglat.lat);
    console.log("val", val);
    setObj(val.target._points);
  };

  const getPointsAction = () => {
    getData();
  };

  const clearPointsAction = () => {
    setPointData([]);
  };

  const clearAllAction = () => {
    map.clearMap();
  };

  const handleSearch = (newValue: string) => {
    if (newValue) {
      searchPlace();
    } else {
      setFilterList([]);
    }
  };

  const handleChange = (val: any, option: any) => {
    // console.log("newValue", val, option, address);
    const obj = {name: option.info.name, lng: option.info.location.lng, lat: option.info.location.lat, id: option.info.id, remark: option.info.address};
    const arr = JSON.parse(JSON.stringify(pointData));
    if (arr.length) {
      // const fd = arr.find((item: any) => item.id === obj.id);
      // if (fd) {

      // }
    } else {
      arr.push(obj);
    }
    console.log("arr", arr);
    setAddress(obj);
    setPointData(arr);
  };

  console.log("PointData", pointData);
  return <div className={sty.main}>
    <div className={sty.filter}>
      <Select
        showSearch
        value={address.code}
        placeholder="请输入区域地址"
        style={{ width: "3rem" }}
        defaultActiveFirstOption={false}
        showArrow={false}
        filterOption={false}
        onSearch={handleSearch}
        onChange={(val: any, option: any) => handleChange(val, option)}
        notFoundContent={null}
      >
        {
          filterList.map((item: any, index: number) => {
            return <Select.Option key={index} code={item.id} info={item}>{item.name}</Select.Option>;
          })
        }
      </Select>
    </div>
    <div id="mapcontainer" />
    <div
      className={sty.tit}
      style={{ 
        left: obj[0],
        top: obj[1],
        transform: "translate(-50%, -50%)"
      }}
    >
      弹框类容
    </div>
    <div className={sty.pos}>
      <div onClick={getPointsAction}>添加点位</div>
      <div onClick={clearPointsAction}>清空点位</div>
      <div onClick={clearAllAction}>清除所有覆盖物</div>
    </div>
  </div>;
};

export default One;
