import React, { useState } from "react";
import One from "./one/one";
import Two from "./two/two";
import Three from "./three/three";
import sty from  "./home.module.less";

const LIST = [
  { code: "one", name: "拖拽" },
  { code: "two", name: "地图" },
  { code: "three", name: "测试" },
];

const compObj = {
  "one": <One />,
  "two": <Two />,
  "three": <Three />,
};

const Home: React.FC = () => {
  const [select, setSelect] = useState<string>("three"); // 拖拽项的数据

  return <div className={sty.content}>
    <div>
      {
        LIST.map((item: any, index: number) => {
          return <div key={index} onClick={() => setSelect(item.code)} className={item.code === select ? sty.selectClas : ""}>{item.name}</div>;
        })
      }
    </div>
    <div>{compObj[select]}</div>
  </div>;
};

export default Home;
