import React from "react";
import { withRouter } from "react-router-dom";
import "./header.less";
import logo from "assets/images/logo.jpg";
import home from "assets/images/12.png";
import { useAction, useStore } from "store/store";
import commonFunc from "untis/common-func";

const Header: React.FC = (props: any) => {
  const store = useStore();
  const action = useAction();

  const layAction = () => {
    action.changeIsMenu(!store.isMenu);
  };

  const toHomeAction = () => {
    props.history.push("/honme");
  };
  const closePage = () => {
    sessionStorage.setItem("isLogin", "false");
    commonFunc.removeCookie("token");
    action.changeHasAuth(false);
  };

  return (
    <div className="page-show">
      <div className="show-left">
        <img src={logo} alt="" onClick={layAction} />
        <div className="title">徐隆基</div>
      </div>
      <div className="show-right">
        <img onClick={toHomeAction} src={home} alt="" />
        <div className="btn" onClick={closePage}>
          推出登录
        </div>
      </div>
    </div>
  );
};

export default withRouter(Header);
