import * as UI from "./actionType";

const defaultState = {
  rootFontSize: 66.66666666666666,
  userInfo: {},
  hasAuth: false,
  isMenu: true,
  fullScreen: false
};

const appReducer = (state = defaultState, action) => {
  switch (action.type) {
    case UI.ROOT_FONT_SIZE:
      return { ...state, rootFontSize: action.value };
    case UI.USER_INFO:
      return { ...state, userInfo: action.value };
    case UI.HAS_AUTH:
      return { ...state, hasAuth: action.value };
    case UI.ISMUNE:
      return { ...state, isMenu: action.value };
    case UI.FULLSCREEN:
      return { ...state, fullScreen: action.value };
    default:
      return state;
  }
};

export { appReducer, defaultState };
