import React, { useRef, useEffect, useState } from "react";
import { Input } from "antd";
import sty from  "./a.module.less";

const A: React.FC = () => {
  const myRef: any = useRef(null);
  const [keyWord, setKeyWord] = useState("");
  console.log("bbb");

  useEffect(() => {
    console.log("aaa");
  }, []);

  const changeKeyWord = (e: any) => {
    setKeyWord(e.target.value);
  };

  const btnAction  = () => {
    console.log("ref", myRef);
    myRef.current.focus();
  };

  return <div className={sty.content}>
    <div>
      <Input ref={myRef} placeholder="请输入" value={keyWord} onChange={changeKeyWord} />
      <div onClick={btnAction}>获取焦点</div>
    </div>
  </div>;
};

export default A;
